import _ from 'lodash';
import React, { useState,useRef } from 'react';
import {JSX} from '../../models/reactModels';
import  {DropdownLicense} from '../../models/license';
import {updateTableDevice} from '../../utils/api';
import {BUTTON_SUBMIT,MESSAGE_ALERT1,MESSAGE_ALLERT2,ALERT} from '../../utils/constants';
import styles from './Dropdownlist.module.css';

const Dropdownlist:React.FC<DropdownLicense>=(props):JSX=>{
  

  const {licenseId,expireDate,index,devices,osLicense} = props;
  const indexRowref = useRef<HTMLSelectElement>(null)
  const [pairSelect,setPairSelect]=useState<string>();
  const [device,setDevice]=useState<string>();
  const statusSelection:string[]=['select','pair','eject'];
  const devFiltered = devices && devices.filter( (val: any) => val["device"]["idDevice"] === device && val["device"]);
  const handlerPairSelection=(e:React.ChangeEvent<HTMLSelectElement>)=>setPairSelect(e.target.value);
  const handlerOptiondevices=(e:React.ChangeEvent<HTMLSelectElement>)=>setDevice(e.target.value);
  
  const handlerTriggeringDeviceTable = () => {
    if ((device !== "select" && pairSelect !== "select") && (device !== undefined && pairSelect !== undefined) && (osLicense===devFiltered[0]["device"]["osDevice"]) ){
      
      let status = _.isEmpty(devFiltered) !== true && devFiltered[0]["device"]["status"];
      
      const index = indexRowref.current!.getAttribute("data-index");
      const licenseId = indexRowref.current!.getAttribute("data-licenseid");
      const expiredate = indexRowref.current!.getAttribute("data-expiredate");
      
      if (status === "pending") {
        updateTableDevice({ device, pairSelect, index, licenseId, expiredate });
      } else {
        const messageContent: { key1: string; textSelected: string,title:string } = { key1: "selected",  textSelected: (MESSAGE_ALERT1),title:ALERT};
        props.handlerModalAlert(messageContent);
      }
    }else {
      const messageContent: { key2: string; textCombine: string,title:string } = { key2: "no-selection", textCombine:(MESSAGE_ALLERT2) ,title:ALERT};
      props.handlerModalAlert(messageContent);
    }
  };
  return (
    <div className={styles.containerDropdown}>
      <select  className={styles.dropDownStyle} ref={indexRowref} data-index={index} data-licenseid={licenseId} data-expiredate={expireDate}
        disabled={props.disable} onChange={handlerOptiondevices} >
          <option>select</option>
          {devices &&  devices.map((option:any,i:number)=>{return <option key={i}>{option['device']['idDevice']}</option> })}
      </select>
      <select  className={styles.dropDownStyle} disabled={props.disable} onChange={handlerPairSelection} >
          {statusSelection.map((status:string,i:number)=><option key={i}>{status}</option>)}
      </select>
      <button className={styles.styleButtonDropDownlist} disabled={props.disable} onClick={handlerTriggeringDeviceTable} >
        {BUTTON_SUBMIT}
      </button>
    </div>
  );
};

export default Dropdownlist;
