import TableRowdevices from './TableRowdevices';
import {Device} from '../../models/device';
import Table from '../Table/Table';
import {TABLE_EMPTY} from '../../utils/constants';
import styles from './Tabledevices.module.css';



const Tabledevices:React.FC<{devices: {} | Device | undefined }> = (props):JSX.Element =>{

    const {devices:arrDevices}:Device = props ;
    const devices = arrDevices && arrDevices.map((device:any)=>device!.device)
    const names:string[] = ['Id Device','OS Device','OS Version','Status','License Paired','License Selected']
    return (
      <>
        {devices ? (
          <Table
            styles={styles}
            title={"Devices"}
            th={names.map((name:string,i:number) => (
              <th key={i}>{name}</th>
            ))}
            td={devices.map((device: any, i: number) => {
              return (
                <TableRowdevices
                  key={i.toString()}
                  indexKey={device["idDevice"]}
                  idDevice={device["idDevice"]}
                  osDevice={device["osDevice"]}
                  osVersion={device["osVersion"]}
                  status={device["status"]}
                  licensePaired={device["licensePaired"]}
                  licenseSelected={device["licenseSelected"]}
                />
              );
            })}
          />
        ):<div className={styles.emptyContent}><label>{TABLE_EMPTY}</label></div>}
      </>
    );};

export default Tabledevices;