import _ from 'lodash'; 
import { initializeApp } from "firebase/app";
import { firebaseConfig } from "./configuration";
import { getDatabase, ref, set,onValue,update } from "firebase/database";


export const setDataLicenseToDb = (licensesObj:any):void => {
    const app = initializeApp(firebaseConfig);
    const db = getDatabase(app);
    set(ref(db, "licenses/"), licensesObj);
};

export const retrieveLicenseFromDb=(retrieveLicense:any):any=>{
    const app = initializeApp(firebaseConfig);
    const db = getDatabase(app);
    const starCountRef = ref(db, "licenses/");
    let result:any=null;
    onValue(starCountRef, (snapshot) => {
      const data = _.orderBy(snapshot.val(),'license.os','asc');
      retrieveLicense(data);
      console.log('licenses retrieved from Db.')
    });
  return result;  
};

export const setDevicesToDb = (devices:any):void => {
  const app = initializeApp(firebaseConfig);
  const db = getDatabase(app);
  set(ref(db, "devices/"), devices);
  console.log('New liscense setted up to Db!');
};

export const retrieveDevicesFromDb=(retrieveDevice:(val:Object)=>void):void=>{
  const db = getDatabase();
  const startDeviceRef = ref(db, "devices/");
  try{
    onValue(startDeviceRef, (snapshot) => {
      const data = snapshot.val();
      retrieveDevice(data);    
      console.log('Devices retrieved from Db.')
    });
  }catch(error){
    console.error(error);
  }
};

export const updateTableDevice = (obj: {
  device: string | undefined,
  pairSelect: string | undefined,
  index: string | null,
  licenseId: string | null,
  expiredate: string | null,
}): void => {
  let index: string = "";
  let device: string = "";
  const db: any = getDatabase();

  if (obj["device"] === "device0") {
    index = 0 as unknown as string;
    device = "device0";
  } else if (obj["device"] === "device1") {
    index = 1 as unknown as string;
    device = "device1";
  } else if (obj["device"] === "device2") {
    index = 2 as unknown as string;
    device = "device2";
  } else if (obj["device"] === "device3") {
    index = 3 as unknown as string;
    device = "device3";
  } else if (obj["device"] === "device4") {
    index = 4 as unknown as string;
    device = "device4";
  } else if (obj["device"] === "device5") {
    index = 5 as unknown as string;
    device = "device5";
  }
  const updates: any = {};
  const updates2: any = {};
  const updates3: any = {};
  const updates4: any = {};
  try {
    if (
      (obj["pairSelect"] !== "select" && obj["pairSelect"] !== undefined) ||
      (obj["device"] !== "select" && obj["device"] !== undefined)
    ) {
      updates[`/devices/${index}/device/status`] = obj["pairSelect"];
      updates2[`/licenses/${obj["index"]}/license/disable`] = true;
      updates3[`/devices/${index}/device/licenseSelected`] = obj["licenseId"];
      updates4[`/devices/${index}/device/licensePaired`] =
        obj["expiredate"] + ", " + device;
      update(ref(db), updates);
      update(ref(db), updates2);
      update(ref(db), updates3);
      update(ref(db), updates4);
    }
  } catch (error) {
    console.error(error);
  }
};

