import { Table_ } from '../../models/reactModels';

const Table:React.FC<Table_>=(props)=>{
    
    const {th,td,title,styles} = props;
    return (
        <div className={styles.tableContainer}>
        <h1 className={styles.titleTable}>{title}</h1>
        <table key="table-data" className={styles.tableStyle} >
          <thead key="thead-table-data">
            <tr key="tr-table-data" >
              {th}
            </tr>
          </thead>
          <tbody key="tbody-key">
              {td}   
          </tbody>
        </table>
        </div>
    );
};

export default Table;


