import _ from "lodash";
import ContainerPanel from "../components/ContainerPanel/CointainerPanel";
import { csvReader } from "../helpers/FunctionsHelper";
import React, { useState, useEffect } from "react";
import Tablelicenses from "../components/TableLicense/TableLicense";
import Tabledevices from "../components/Tabledevices/Tabledevices";
import { useHistory } from 'react-router';
import { retrieveLicenseFromDb, retrieveDevicesFromDb } from "../../src/utils/api";
import { Tooltip } from "@mui/material";
import { messageTooltip } from "../utils/constants";
import Modal from "../components/Modal/Modal";
import TableLicensesRefused from "../components/TableLicensesRefused/TableLicensesRefused";
import { Device } from "../models/device";
import { CSV_LOAD_MESSAGE,TITLE_MESSAGE_PANEL,DEFAULT_USER,BUTTON_LOAD,TOOLTIP_BACK_TO_LOGIN,TITLE_TABLE_REFUSED } from "../utils/constants";
import {useStore}  from '../hooks-store/store';
import styles from "./Panel.module.css";

const Panel: React.FC<{ userPanel: string }> = (props): JSX.Element => {

  const history = useHistory();
  const state = useStore()[0];
  const [modal, setModal] = useState<boolean>();
  const [dataLicenses, setDataLicense] = useState<Device>();
  const [dataDevices, setDataDevice] = useState<Device | {}>();
  const [modalAllert, setModalAllert] = useState<boolean>();
  const [textContent, setTextContent] = useState<any>();
  const [username,setUsername] =useState<string|null>(); 
  const stylesModal1 = modal ? [styles.modalLicenseRefused,styles['open']] : [styles.modalLicenseRefused,styles['closed']];
  const stylesModal2 = modalAllert ? [styles.modalAlert,styles['open']] : [styles.modalAlert,styles['closedModalAlert']];

  
  
  const csvReaderHandler = (e: React.ChangeEvent<HTMLInputElement>): void => {
    csvReader(e);
    setTimeout(() => {
      retrieveLicenseFromDb(setDataLicense);
      retrieveDevicesFromDb(setDataDevice);
    }, 1000);
  };

  const handlerModalAlert = (text: string): void => {
    setTextContent(text);
    setModalAllert((prevState) => !prevState);
  };

  const handlerModal = ():void => setModal((prevState) => !prevState);
  
  const handlerBackToLoginPage =()=> history.push('/licensepanel-welcome');

  
  useEffect(() => {
    dataLicenses === undefined && retrieveLicenseFromDb(setDataLicense);
    dataLicenses === undefined && retrieveDevicesFromDb(setDataDevice);
    
    if(!_.isEmpty(state.username) ){
      sessionStorage.setItem('uid',state.username);
      setUsername(sessionStorage.getItem('uid'));
    }else{
      setUsername(sessionStorage.getItem('uid'));
    }
    
  }, [dataLicenses,state.username]);

  useEffect(()=>{
    return()=>{
      sessionStorage.clear();
      console.log('Component Panel Unmounted!')
    }
  },[]);




  return (
    <ContainerPanel>
      <h1> {TITLE_MESSAGE_PANEL} {username === null ? DEFAULT_USER : username} </h1>
      <Tabledevices devices={dataDevices} />
      {dataLicenses && _.isEmpty(dataLicenses) !== true ? (
      <Tablelicenses devices={ dataDevices } licenses={ dataLicenses } handlerModalAlert={handlerModalAlert} />
      ) : (
      <div>
          <p className = { styles.titleTablelicense }>
            {CSV_LOAD_MESSAGE} 
          </p>
      </div>
      )}
      <div className={ styles.containerButtonCSV } >
        <label className={ styles.csvButton } >
          {BUTTON_LOAD}
          <input className={ styles.buttonFile } type="file" name="Load CSV" onChange={ csvReaderHandler } />
        </label>
      </div>
      {!_.isEmpty(dataLicenses) && (
        <Tooltip title = { messageTooltip } onClick={ handlerModal }>
          <div className = { styles["circle-handle-table-refused"] }>
            <span className = { styles.lineLeft } />
            <span className = { styles.lineRight } />
          </div>
        </Tooltip>
      )}
      {!_.isEmpty(dataLicenses) && (
        <Tooltip title = {TOOLTIP_BACK_TO_LOGIN} onClick={handlerBackToLoginPage}>
          <div className = { styles["circle-handle-back-login"] }>
            <span className = { styles["angle-bracket-1"] } />
            <span className = { "angle-bracket-2" }/>
          </div>
        </Tooltip>
      )}
        
        <Modal
          tag={modal}
          styles= {stylesModal1}
          content = { <TableLicensesRefused devices={dataDevices} title={TITLE_TABLE_REFUSED} />}
          handlerModal = { handlerModal }
          keyContent = "table"
        />
         
        <Modal
          tag={modalAllert}
          styles={stylesModal2}
          content= {modalAllert && (textContent["key2"] === "no-selection" ? textContent["textCombine"] : textContent["textSelected"]) } 
          handlerModal = { handlerModalAlert }
          keyContent = {modalAllert && textContent["key2"] }
          title = {modalAllert && textContent["title"]}
        />
    </ContainerPanel>
  );
};

export default Panel;
