import { Redirect, Route, Switch } from 'react-router';
import Welcome from "./components/Welcome/Welcome";
import Panel from "./components/Panel";
import Sampl from "./components/Sampl/Sampl";
import {JSX} from './models/reactModels'
import { useState } from 'react';



const App:React.FC<{}>=():JSX=>{
    
    const  [userPanel,setUserpanel]=useState<string>();
    const getUsername=(user:string):void=> setUserpanel(user);

    return (
      <>
        <Switch>
          <Route path="/" exact>
            <Redirect to="/licensepanel-welcome" />
          </Route>
          <Route path="/licensepanel-welcome">
            <Welcome getUsername={getUsername} />
          </Route>
          <Route path="/panel">
            <Panel userPanel={userPanel!} />
          </Route>
          <Route path="/sampl">
            <Sampl />
          </Route>
        </Switch>
      </>
    );

};

export default App;