import {RowDevice} from '../../models/device';
import { JSX } from '../../models/reactModels';


const TableRow:React.FC<RowDevice> = (props):JSX => {
    
  const {idDevice, osDevice, osVersion,status,licensePaired,licenseSelected} = props;
  
  return (
      <tr >
        <td >{idDevice}</td>
        <td >{osDevice}</td>
        <td >{osVersion}</td>
        <td >{status}</td>
        <td >{licensePaired}</td>
        <td>{licenseSelected}</td>
      </tr>
    );
  };

  export default TableRow;