import _ from 'lodash';
import {LicenseRefuse} from '../../models/license';
import {JSX} from '../../models/reactModels';
import Table  from '../Table/Table';
import {TITLE_TABLE_REFUSED} from '../../utils/constants';
import styles from './TableLicensesRefused.module.css';



const TableLicensesRefused:React.FC<LicenseRefuse> = (props):JSX =>{
    
    const {devices}:any = props;
    const licensesRefused = devices  && devices.filter((obj:any)=>obj['device']['status']==='eject');
    const namesMainRow:string[] = ["Id Device","OS Device","OS Version","Status","License Paired","License Selected"];
    
    return (
      <>
        {!_.isEmpty(licensesRefused) ? (
          <Table 
            th = {namesMainRow.map((nameMainrow: string, i: number) => (
              <th key={i}>{nameMainrow}</th>
            ))}
            td = {licensesRefused.map((obj: any, i: number) => {
              return (
                <tr key={i}>
                  <td>{obj["device"]["idDevice"]}</td>
                  <td>{obj["device"]["osDevice"]}</td>
                  <td>{obj["device"]["osVersion"]}</td>
                  <td>{obj["device"]["status"]}</td>
                  <td>{obj["device"]["licensePaired"]}</td>
                  <td>{obj["device"]["licenseSelected"]}</td>
                </tr>
              );
            })}
            title = {TITLE_TABLE_REFUSED}
            styles = {styles} />
        ):<div>No liceses Found.</div>}
      </>
    );
  };

export default TableLicensesRefused;