import { setDataLicenseToDb } from '../utils/api';
import { setDevicesToDb } from '../utils/api';
import { License } from '../models/license';
import { devices } from '../utils/device';

export const csvReader = (event:React.ChangeEvent<HTMLInputElement>) => {
  

  const element:HTMLInputElement = event.target;
  const csv:File = element.files![0];
  
    
  const reader:FileReader = new FileReader();
  const licenses:{[key:string]:License} = {};

  (csv !==undefined) && reader.readAsText(element && csv);
    
    reader.onload = function (event) {
      // The file's text will be printed here
      const rows = event.target!.result as string;

      const newArr = rows.split("\r\n").map((row) => {
        return row.split(";");
      });
      
      const copyLicenses = [...newArr];
      // eslint-disable-next-line
      const [namesLicenses, ...valsLicenses] = copyLicenses;

      valsLicenses.forEach((valLicense:string[], i:number,_) => {
        let nestObj:any = {'license':{'licenseId':'','expireDate':'','os':'','index':i,'disable':false }}
        licenses[`${i}`] = nestObj;
        valLicense.forEach((elem:string , _ , arr:string[]) => {
          nestObj['license']['licenseId'] = arr[0];
          nestObj['license']['expireDate'] = dateFormatter(arr[1]);
          nestObj['license']['os'] = arr[2];
        });
      });
      setTimeout(()=>setDataLicenseToDb(licenses),3000);
      
      //Simulation load Devices in async way. 
      setTimeout(()=>setDevicesToDb(devices),2000);      
    };
};

const dateFormatter =(date:string):string=>{
  const year:string = date.substring(0,4);
  const month:string = date.substring(4,6);
  const day:string = date.substring(6,8);
  const time:string = date.substring(8,12);
  const hours:string = time.substring(0,2);
  const minutes:string = time.substring(2,4);
  const dataFormatted:string = day+'/'+month+'/'+year+', '+hours+':'+minutes;
  return dataFormatted;
};