import TableRow from './Tablerows';
import {License} from '../../models/license';
import {JSX} from '../../models/reactModels';
import {TITLE_LICENSE_TABLE} from '../../utils/constants';
import {TABLE_EMPTY} from '../../utils/constants';
import Table from '../Table/Table';
import styles from './TableLicense.module.css';


const Tablelicenses:React.FC<License> = (props):JSX =>{
    
    const {licenses,devices}:any=props;
    const names:string[] =['License Id','Expire Date', 'Operative System','Pair Device'];
    return (
      <>
        {licenses ? (
          <Table
            styles={styles}
            title={TITLE_LICENSE_TABLE}
            th={names.map((name:string,i:number)=>(<th key={i}>{name}</th>))}
            td={licenses.map((obj: any, i: number) => {
              return (
                <TableRow
                  devices={devices}
                  key={i.toString()}
                  index={obj["license"]["index"]}
                  disable={obj["license"]["disable"]}
                  expireDate={obj["license"]["expireDate"]}
                  licenseId={obj["license"]["licenseId"]}
                  operativeSystem={obj["license"]["os"]}
                  handlerModalAlert={props.handlerModalAlert}
                />
              );
            })}
          />
        ):<div className={styles.emptyContent}><label>{TABLE_EMPTY}</label></div>}
      </>
    );
};

export default Tablelicenses;