import {JSX} from '../../models/reactModels';
import Backdrop  from './Backdrop';
import { Modalprops } from '../../models/reactModels';
import styles from './Modal.module.css';

const Modal:React.FC<Modalprops> =(props):JSX=>{
    
    const {keyContent,tag} =props;


    
    return(
        <>
          <div className={props.styles!.join(' ') }>
            <Backdrop handlerModal={props.handlerModal}/>
          {tag &&  <div className={(keyContent === "no-selection" || keyContent===undefined) ? styles['modal-alert'] : styles.modal}>
                {(keyContent === "no-selection" || keyContent===undefined) && <h1>{props.title}</h1>}
                <div style={{textAlign:'center'}}>{props.content}</div>
                <button className={styles.buttonModal} onClick={props.handlerModal}>Close</button>
            </div>
          }
          </div>
        
        </>
    );
};

export default Modal;