import Dropdownlist from "../Dropdownlist/Dropdownlist";
import {JSX} from '../../models/reactModels';
import {Tablerow} from '../../models/license';

const TableRow:React.FC<Tablerow> = (props):JSX => {

    const {
      disable,
      expireDate,
      licenseId,
      operativeSystem,
      index,
    }: {
      disable: boolean;
      expireDate: string;
      licenseId: string;
      operativeSystem: string;
      index: number;
    } = props;
    const devices =props.devices;
    return (
      <tr>
          <td >{licenseId}</td>
          <td>{expireDate}</td>
          <td>{operativeSystem}</td>
          <td> <Dropdownlist 
                  expireDate={expireDate}
                  licenseId={licenseId}
                  index={index}
                  disable={disable}
                  devices={devices}
                  handlerModalAlert={props.handlerModalAlert}
                  osLicense = {operativeSystem}
                  />
          </td>
      </tr>
    );
  };

  export default TableRow;