import { useState } from 'react';
import { useHistory } from 'react-router';
import {JSX} from '../../models/reactModels';
import {TITLE_PANEL_WELCOME} from '../../utils/constants';
import {useStore}  from '../../hooks-store/store';
import styles from './CardLogin.module.css';

const Card:React.FC<{getUsername:(x:string)=>void}>=(props):JSX=>{
    
    const dispatch = useStore()[1];
    const [user,setUser] = useState<string>();
    const history=useHistory();
    
    const handlerInput=(e:React.ChangeEvent<HTMLInputElement>)=>{
        const value = e.target.value;
        setUser(value);        
    }

    const redirectUser=()=>{
        history.push('/panel');
        props.getUsername(user!);
        dispatch('SET_USERNAME',user)
    };
    
    return(
        <div className = {styles.containerCard} > 
            <h1>{TITLE_PANEL_WELCOME}</h1>
            <div className={styles.containerInput}>
                <label>Username:</label>
                <input type="text" onChange={handlerInput}/>
            </div>
            <button onClick={redirectUser}>Enter</button>
        </div>
    )
};

export default Card;