import {JSX} from '../../models/reactModels';
import CardLogin from '../CardLogin/CardLogin';
import styles from './Welcome.module.css';


const  Welcome:React.FC<{getUsername:(user:string)=>void}> = (props):JSX=>{
  return (
      <div className={styles.containerWelcome}>
          <CardLogin getUsername={props.getUsername} />      
      </div>
      
      )
};

export default Welcome;